/* 🌟 기존 스타일 유지 */
.app-container {
  text-align: center;
  font-family: Arial, sans-serif;
  background-color: #121212;
  color: white;
  min-height: 100vh;
  padding: 20px;
}

/* ✅ 카테고리 버튼 스타일 */
.category-grid {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 10px;
}

.category-grid button {
  background-color: #333;
  color: white;
  border: 1px solid #555;
  padding: 10px 20px;
  cursor: pointer;
  border-radius: 5px;
}

.category-grid button:hover {
  background-color: #444;
}

/* ✅ 선택된 카테고리 스타일 */
.category-button.selected {
  background-color: #e94560;
  font-weight: bold;
  border: 2px solid white;
}

/* ✅ 질문 표시 스타일 */
.question-box {
  margin-top: 20px;
  padding: 15px;
  background-color: #222;
  border-radius: 8px;
  display: inline-block;
  text-align: left;
}

.question-box h3 {
  margin: 0;
  font-size: 18px;
  color: #ffcc00;
}

.question-box p {
  font-size: 16px;
}

/* ✅ 사용자 응답 스타일 */
.answer-section {
  margin-top: 20px;
  padding: 15px;
  background-color: #333;
  border-radius: 8px;
  display: inline-block;
  text-align: left;
}

.answer-section h3 {
  margin: 0;
  font-size: 18px;
  color: #ff6600;
}

.answer-section audio {
  width: 100%;
}

/* ✅ 버튼 스타일 */
.control-button {
  background-color: #007bff;
  color: white;
  border: none;
  padding: 10px 15px;
  margin: 5px;
  cursor: pointer;
  border-radius: 5px;
  font-size: 16px;
}

.control-button:hover {
  background-color: #0056b3;
}

/* ✅ 버튼 컨테이너 정렬 */
.button-group {
  display: flex;
  justify-content: center;
  gap: 15px;
  margin-bottom: 20px;
}

/* ✅ 녹음 중 애니메이션 효과 */
.recording-indicator {
  color: red;
  font-weight: bold;
  margin-top: 10px;
  animation: blink 1s infinite;
}

@keyframes blink {
  50% {
    opacity: 0.5;
  }
}

/* ✅ 음성 자막 표시 스타일 */
.transcript-box {
  margin-top: 20px;
  padding: 15px;
  background-color: rgba(0, 0, 0, 0.8);
  border-radius: 8px;
  text-align: center;
  font-size: 18px;
  color: #fff;
  font-weight: bold;
}

/* ✅ API 키 입력 섹션 */
.api-key-section {
  margin-top: 20px;
}

.api-key-section input {
  padding: 10px;
  width: 250px;
  border-radius: 5px;
  border: 1px solid #ccc;
  font-size: 16px;
}

.api-key-section button {
  margin-left: 10px;
  padding: 10px 15px;
  font-size: 16px;
  border: none;
  background-color: #28a745;
  color: white;
  border-radius: 5px;
  cursor: pointer;
}

.api-key-section button:hover {
  background-color: #218838;
}